
div#pageHeader > div > h2 {
  margin-top: 0.4em;
}
.ui.container, #mainContainer {
  width: 1072px !important;
}
div#mainContainer {
  margin-top: 4em;
}
p.form-error {
  color: red;
  font-size: 0.9em;
}
.ui.vertical.menu .item > i.icon.left {
  float: none;
  margin: 0 0.35714286em 0 0;
}
div.form-actions {
  margin-top: 2.5em;
  text-align: center;
}
div#pageHeader {
  margin-bottom: 0.5em;
}
div.page-actions {
  margin-top: 0.7em;
  text-align: right;
}
div.header.item {
  background: #f0f0f0 !important;
}
div.page-buttons {
  text-align: right;
}
i.clickable {
  cursor: pointer;
}
div.empty {
  margin-top: 5em;
  text-align: center;
}
div.page-buttons > span {
  margin-right: 1em;
}
div.page-actions > div.ui.input.search {
  width: 55%;
}
div.page-actions > a.ui.button {
  margin-left: 1em;
}
.ui.body.segment {
  padding: 2em 2.5em;
}
.ui.title.segment {
    background: #e9e9e9;
}
input#code {
    letter-spacing: 0.33em;
    text-align: center;
}

.device-expires {
  color: #999;
  font-size: 0.945em;
  padding-top: 0.33em;
}
div.console {
  font-family: "Consolas",sans-serif;
  font-size: 0.9em !important;
  padding: 1.5em !important;
}

tr.form-table-row td > p.help {
  color: #888;
  margin-top: 0.4rem;
}
tr.form-table-row td:first-child {
  width: 55%;
}
tr.form-table-row td:last-child {
  vertical-align: middle;
}

tr.rule.disabled > td:last-child {
  pointer-events: auto !important;
}
tr.rule.disabled > td:last-child > button {
  margin-left: 1em;
}

.log {
  background: #f6f6f6;
  font-family: "Menlo", "Consolas", sans-serif;
  font-size: 0.85em;
  height: 650px;
  overflow-y: scroll;
  padding: 1em;
}

a.disabled {
  font-style: italic;
  text-decoration: line-through;
}
